@import "variable";

a {
  text-decoration: none !important;

  &:focus { // IE 브라우저 클릭 후 점선 생기는 현상 수정
    outline: none;
  }
}

input[type="button"] {
  outline: none;
}

input[type="button"]::-moz-focus-inner {
  border: 0;
}

*:focus {
  outline: 0 !important;
}

img {
  &:focus { // IE 브라우저 클릭 후 점선 생기는 현상 수정
    outline: none;
  }
}

ul {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

input:focus {
  outline: none;
}

textarea:focus {
  outline: none;
}

html {
  max-width: 1024px;
  padding: 0 100px;
  margin: auto;
  scroll-behavior: smooth;
  background: $COLOR_DARK;
}
body {
  font-family: $BASE_FONT_KR !important;
  font-weight: $FONT_WEIGHT_REGULAR;
  background-color: transparent !important;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  #root {
    min-height: 100vh;
    .home-btn {
      @include setCursorPointer();
      position: fixed;
      top: 23px;
      left: 20px;
      @include setFont($COLOR_FFFFFF, $FONT_WEIGHT_BOLD, 20px);
    }
  }
}


